hooks.setMenuHook("sync", {
  primary: "Sync",
  icon: <SyncIcon />,
  href: "#/sync",
});
hooks.setMenuHook("settings", {
  primary: "Settings",
  icon: <SettingsIcon />,
  href: "#/apps/waziup.wazigate-system/ui/",
});
